import React, { useState, useEffect } from "react";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {
  Autocomplete,
  Box,
  Card,
  FormControlLabel,
  TextField,
  Typography,
  Checkbox,
} from "@material-ui/core";
import toast from "react-hot-toast";

import { Formik } from "formik";
import Save from "../Buttons/Save";
import ResetButton from "../Buttons/ResetButton";
import Design from "../Design";
import countries from "../../components/dashboard/account/countries";
import { customAxios } from "../apis/Axios";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
      margin: "17px",
    },
  },
  typography: {
    padding: "17px",
  },
  head: {
    height: "200vh",
    paddingLeft: 30,
    paddingRight: 30,
    paddingTop: 10,
    background: "none",
  },
}));

export default function Specific_Sms() {
  const classes = useStyles();
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [options, setOptions] = useState([]);
  const handleOptionChange = (event, values) => {
    setSelectedOptions(values);
  };
  const handleSelectAll = () => {
    setSelectedOptions(options);
  };

  const handleClearAll = () => {
    setSelectedOptions([]);
  };
  useEffect(() => {
    const fetchOptions = async () => {
      const response = await customAxios.getCall("list/search");

      setOptions(response);
    };
    fetchOptions();
  }, []);

  useEffect(() => {
    console.log(selectedOptions);
    console.log(options);
  }, [selectedOptions]);

  return (
    <>
      <Card className={classes.head}>
        <Formik
          // enableReinitialize
          initialValues={{
            rollno: "",
            detail: "",
            Student: options,
          }}
          validationSchema={Yup.object().shape({
            rollno: Yup.string().max(255).required("Rollno is required"),
            studentname: Yup.string().max(255).required("Password is required"),
          })}
          onSubmit={async (values) => {
            console.log("val", values);
            await new Promise((r) => setTimeout(r, 500));

            alert(JSON.stringify(values, null, 2));
            toast.success("pay success full");
          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            dirty,
            touched,
            values,
            handleReset,
          }) => (
            <div>
              <Design text="SMS Notification" dashText="SMS Notification" />
              <Box style={{ paddingLeft: 20 }} boxShadow={20} color="black">
                <div className={classes.typography}>
                  <Typography variant="h5">
                    <strong>SMS Notification</strong>
                  </Typography>
                </div>
                <form noValidate onSubmit={handleSubmit}>
                  <div>
                    <TextField
                      autoComplete="off"
                      id="rollno"
                      label="Roll No"
                      style={{ margin: 8, width: "95%" }}
                      variant="outlined"
                      type="number"
                      error={Boolean(touched.rollno && errors.rollno)}
                      helperText={touched.rollno && errors.rollno}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.rollno}
                    />
                    <Box style={{ display: "flex", alignItems: "start", flexDirection:"column"}}>
                      <Box style={{flexDirection:"row" ,marginLeft:8}}>

                      <FormControlLabel
                        control={
                          <Checkbox
                          checked={selectedOptions.length === options.length}
                            onChange={handleSelectAll}
                          />
                        }
                        label="Select All"
                      />
                      <FormControlLabel
                        control={
                          <Checkbox
                            checked={selectedOptions.length === 0}
                            onChange={handleClearAll}
                          />
                        }
                        label="Clear All"
                      />
                        </Box>
                      <Autocomplete
                        style={{ margin: 8, width: "95%" }}
                        multiple
                        getOptionLabel={(option) => option.name}
                        options={options}
                        value={selectedOptions}
                        onChange={handleOptionChange}
                        renderInput={(params) => (
                          <TextField
                            fullWidth
                            label="Student"
                            name="Student"
                            // onChange={handleChange}
                            variant="outlined"
                            {...params}
                          />
                        )}
                      />
                    </Box>
                   
                    <TextField
                      error={Boolean(touched.detail && errors.detail)}
                      helperText={touched.detail && errors.detail}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      id="detail"
                      label="Detail"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      multiline
                      rows={4}
                      margin="normal"
                      variant="outlined"
                      value={values.detail}
                    />{" "}
                  </div>
                  <Save
                    text="Save"
                    variant="contained"
                    size="medium"
                    color="success"
                    // disableElevation
                    type="submit"
                    disabled={isSubmitting}
                  />
                  {/* <button type="submit" disabled={isSubmitting}>
                    Submit
                  </button> */}
                  <ResetButton
                    text="Reset"
                    variant="contained"
                    size="medium"
                    color="primary"
                    type="button"
                    onClick={handleReset}
                    disabled={!dirty || isSubmitting}
                    // disableElevation
                  />
                </form>
              </Box>
            </div>
          )}
        </Formik>
        <Typography style={{ marginTop: 20 }}>
          © Copyrights <b>akkhor</b> 2019. All rights reserved. Designed by
          PsdBosS
        </Typography>
      </Card>
    </>
  );
}
